.container {
    width: 1070px;
    padding-left: 15px;
    padding-right: 15px;
    margin-left: auto;
    margin-right: auto;
    max-width: 100%;
    position: relative;
}

.column {
	&-1 {
		width: calc(100% / 12);
	}
	&-2 {
		width: calc(100% / 12 * 2);
	}
	&-3 {
		width: calc(100% / 12 * 3);
	}
	&-4 {
		width: calc(100% / 12 * 4);
	}
	&-5 {
		width: calc(100% / 12 * 5);
	}
	&-6 {
		width: 50%;
	}
	&-7 {
		width: calc(100% / 12 * 7);
	}
	&-8 {
		width: calc(100% / 12 * 8);
	}
	&-9 {
		width: calc(100% / 12 * 9);
	}
	&-10 {
		width: calc(100% / 12 * 11);
	}
	&-11 {
		width: calc(100% / 12 * 11);
	}
}