.prev {
	padding-left: rem(21px);
	padding-right: rem(31px);
	font-weight: 800;
	font-size: rem(16px);
	line-height: rem(24px);
	color: $blue;
}

.next {
	padding-right: rem(21px);
	padding-left: rem(31px);
	font-weight: 800;
	font-size: rem(16px);
	line-height: rem(24px);
	color: $blue;
}